import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform, LoadingController } from 'ionic-angular';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';

import { MenuPage } from '../menu/menu';

import { RestService } from '../../services/rest.service';
import { CacheService } from '../../services/cache.service';
/**
 * Generated class for the ScannerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-scanner',
  templateUrl: 'scanner.html',
})
export class ScannerPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, private barcodeScanner: BarcodeScanner, private restService: RestService, private cache: CacheService, public platform: Platform, public loadingCtrl: LoadingController) {

  }

  ionViewDidLoad() {
    let loader = this.loadingCtrl.create({content:'Loading...'});

    if(this.platform.is('core')){
      loader.present();
    }else{
      loader.present();
      this.barcodeScanner.scan().then((barcodeData)=>{
        this.restService.scanIn("")
          .subscribe(
            data => {
              this.cache.put('menu',data.menu);
              this.cache.put('table', data.table);
            },
            error => {
              alert(error);
            }
          );
      });
    }
  }

  navToRoot() {
    this.navCtrl.push(MenuPage);
    this.navCtrl.remove(0,1,null);
  }

}



